<template>
<div>
  <nav class="navbar navbar-default navbar-fixed-top">
    <div class="container">
      <!-- Brand and toggle get grouped for better mobile display -->
      <div class="navbar-header">
        <button @click='hidemenubtn = !hidemenubtn' type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                      <span class="sr-only">Toggle navigation</span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                      <span class="icon-bar"></span>
                    </button>
        <a class="navbar-brand" href="#">WEBGIS</a>
      </div>
      <!-- Collect the nav links, forms, and other content for toggling -->
      <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
        <ul class="nav navbar-nav">
          <li><a href="#">主页 </a></li>
          <li class="dropdown">
            <a href="#" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">各种骚东西 <span class="caret"></span></a>
            <ul class="dropdown-menu">
              <li><a href="#">Action</a></li>
              <li><a href="#">Another action</a></li>
              <li><a href="#">Something else here</a></li>
              <li role="separator" class="divider"></li>
              <li><a href="#">Separated link</a></li>
              <li role="separator" class="divider"></li>
              <li><a href="#">One more separated link</a></li>
            </ul>
          </li>
        </ul>
        <ul class="nav navbar-nav navbar-right">
          <li data-toggle="modal" data-target="#myModal" @click='login'><a href="#">登录 </a></li>
          <li data-toggle="modal" data-target="#myModal" @click='regis'><a href="#">注册 </a></li>
        </ul>
      </div>
      <!-- /.navbar-collapse -->
      <!-- Button trigger modal -->
    </div>
    <!-- /.container-fluid -->
  </nav>
        <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true" data-backdrop="static">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button id='closemodel' type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
              <h4 class="modal-title" id="myModalLabel">{{ login_title }}</h4>
            </div>
            <div class="modal-body">
              <div class="input-group col-xs-10">
                <span class="input-group-addon">用户名 </span>
                <input v-model='uname' type="text" class="form-control" placeholder="邮箱" aria-describedby="sizing-addon2">
              </div>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">密码 </span>
                <input v-model='psword' type="password" class="form-control" placeholder="英文加数字" aria-describedby="sizing-addon2">
              </div>
              <div v-show='show_nickname'>
              <div style='height: 20px'></div>
              <div class="input-group col-xs-10">
                <span class="input-group-addon">昵称 </span>
                <input v-model='nickname' type="text" class="form-control" placeholder="昵称" aria-describedby="sizing-addon2">
              </div>
              </div>
            </div>
            <div class="modal-footer" id='twobtn'>
              <button type="button" class="btn btn-primary" @click='sub'>{{ login_title }}</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  import jquery from 'jquery'
  import connect from '../../static/js/Connector.js'
  export default {
    data () {
      return {
        uname: '',
        psword: '',
        nickname: '',
        show_nickname: false,
        login_title: '',
        ajax_type: '',
        hidemenubtn: false
      }
    },
    methods: {
      sub () {
        var url = 'http://localhost:8080/DesktopService'
        // jquery.ajax({
        //   type: 'POST',
        //   url: url,
        //   data: {
        //     request: JSON.stringify({
        //       type: 'USER_LOGIN',
        //       data: {
        //         username: 'test',
        //         password: 'test',
        //         nickname: 'test'
        //       }
        //     })
        //   },
        //   success: function (data) {
        //     var json = JSON.parse(data)
        //     alert(json.success)
        //     jquery('#closemodel').trigger('click')
        //   },
        //   error: function (data) {
        //     alert('net error')
        //   }
        // })
        var that = this
        var opt = {
          url: url,
          params: {
            type: that.ajax_type,
            data: {
              username: that.uname,
              password: that.psword,
              nickname: that.nickname
            }
          },
          success: function (data) {
            console.log(data)
            alert(data.message)
            jquery('#closemodel').trigger('click')
          },
          failure: function (data) {
            console.log(data)
            alert(data.message)
          }
        }
        connect(opt)
      },
      regis () {
        this.show_nickname = true
        this.login_title = '注册'
        this.ajax_type = 'USER_REGISTE'
      },
      login () {
        this.show_nickname = false
        this.login_title = '登录'
        this.ajax_type = 'USER_LOGIN'
      }
    },
    watch: {
      hidemenubtn: function (oldval, newval) {
        if (newval === false) {
          jquery('#menu-btn').hide()
        } else if (newval === true) {
          jquery('#menu-btn').show()
        }
      }
    }
  }
</script>

<style scoped>
  .modal-dialog {
    top: 30%;
    padding-left: 50px;
    padding-right: 50px
  }
  #myModalLabel,
  #twobtn {
    text-align: center
  }
</style>



