<template>
  <div>
    <nav class='nav nav-default navbar-fixed-top maptab col-md-4 col-md-offset-4'>
      <div class='container'>
      <div class="navbar-header">
        <button id='menu-btn' class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-2" aria-expanded="false">
                        <tag></tag>
                      </button>
                      </div>
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-2">
          <ul class="nav nav-tabs">
            <li @click='enter(pos.xinxiao)'>
              <tag txt='新校区'></tag>
            </li>
            <li @click='enter(pos.nanxiao)'>
              <tag txt='南校区'></tag>
            </li>
            <li @click='enter(pos.benbu)'>
              <tag txt='校本部' href="#"></tag>
            </li>
            <li @click='enter(pos.xiangya)'>
              <tag txt='湘雅' href="#"></tag>
            </li>
            <li @click='enter(pos.tiedao)'>
              <tag txt='铁道' href="#"></tag>
            </li>
            <div class='col-md-3'></div>
            <li @click=''>
              <tag txt='发布我的骚东西' href="#"></tag>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <baidu-map class='bm' :center='position.center' @moving='sync' :scroll-wheel-zoom='scroll' :zoom='position.zoom'>
    <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_LEFT"></bm-navigation>
    <bm-city-list anchor="BMAP_ANCHOR_TOP_LEFT"></bm-city-list>
      <input type='number' v-model="position.center.lng">
      <input type='number' v-model="position.center.lat">
      <input type='number' v-model="position.zoom">
    </baidu-map>
  </div>
</template>

<script>
  import tag from '@/components/tag'
  export default {
    data () {
      return {
        position: {// 点击定位一次后若鼠标拖动缩放   再点击相同地点  会出现错误
          center: {
            lng: 116.404,
            lat: 39.915
          },
          zoom: 18
        },
        scroll: true,
        pos: {
          nanxiao: {
            lng: 112.942658,
            lat: 28.164901
          },
          xinxiao: {
            lng: 112.94644,
            lat: 28.154619
          },
          benbu: {
            lng: 112.935229,
            lat: 28.177018
          },
          xiangya: {
            lng: 112.942577, // 湘雅和铁道位置还没找!
            lat: 28.165049
          },
          tiedao: {
            lng: 112.942577,
            lat: 28.165049
          }
        }
      }
    },
    methods: {
      handler ({
        BMap,
        map
      }) {
        console.log(BMap, map)
        this.lng = 116.404
        this.lat = 39.915
      },
      sync (e) {
        this.position.center = e.target.getCenter()
      },
      enter (target) {
        setTimeout(() => {
          this.position.center = target
        }, 100)
        setTimeout(() => {
          this.position.zoom = 17.9
        }, 100)
        setTimeout(() => {
          this.position.zoom = 18
        }, 100)
      },
      ctrete_new_event () {
        var opt = {
          url: 'http://localhost:8080/DesktopService',
          params: {
            type: 'EVENT_UPLOAD',
            data: {
              event: '',
              happentime: '',
              positionx: '',
              positiony: ''
            }
          }
        }
        console.log(opt)
      }
    },
    components: {
      tag
    }
  }
</script>

<style>
  .bm {
    position: absolute;
    top: 100px;
    left: 10px;
    right: 10px;
    bottom: 60px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2)
  }
  .maptab {
    position: absolute;
    top: 61px
  }
  #menu-btn {
    bottom: 23px;
    left: 26px
  }
</style>



